"use client";
import { useState, useEffect } from "react";
import { BiEdit, BiTrash } from "react-icons/bi";
import useAdminStore from "@/store/adminStore";
import toast from "react-hot-toast";

export default function OurPartnersTable() {
  const { partners, isLoading, getPartners, deletePartner } = useAdminStore();

  useEffect(() => {
    const fetchPartners = async () => {
      try {
        await getPartners({ status: 'approved', limit: 100 });
      } catch (error) {
        console.error("Failed to load partners:", error);
        toast.error("Failed to load partners");
      }
    };
    fetchPartners();
  }, [getPartners]);

  const healthPartners = partners.filter(p => p.category === 'medical' && (p.status === 'approved' || p.isActive));
  const foodPartners = partners.filter(p => p.category === 'food' && (p.status === 'approved' || p.isActive));

  const handleDelete = async (partnerId) => {
    if (confirm("Are you sure you want to delete this partner?")) {
      try {
        await deletePartner(partnerId);
        toast.success("Partner deleted successfully");
        getPartners({ status: 'approved', limit: 100 });
      } catch (error) {
        toast.error("Failed to delete partner");
      }
    }
  };

  return (
    <>
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Partner's</h1>
        <nav className="text-gray-500 text-sm">
          <ol className="flex space-x-2 items-center">
            <li><a href="/admin" className="hover:underline text-gray-600">Home</a></li>
            <li className="text-gray-400">/</li>
            <li className="text-gray-700">Partner's Section</li>
          </ol>
        </nav>
      </div>

      {isLoading ? (
        <div className="text-center py-12">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-500 mx-auto"></div>
          <p className="text-gray-600 mt-4">Loading partners...</p>
        </div>
      ) : (
        <div className="space-y-8">
          {/* Healthcare Partners */}
          <div className="bg-white rounded-xl shadow-sm border p-6">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-xl font-semibold text-blue-900 flex items-center gap-2">
                üè• Healthcare Partners ({healthPartners.length})
              </h2>
            </div>
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="bg-gradient-to-r from-blue-50 to-cyan-50 border-b-2 border-blue-200">
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Business Name</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Registration</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Services</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Contact</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Location</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Discount</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {healthPartners.length > 0 ? (
                    healthPartners.map((partner, idx) => (
                      <tr key={partner._id} className={`border-b hover:bg-gray-50 transition-colors ${idx % 2 === 0 ? 'bg-white' : 'bg-gray-50/30'}`}>
                        <td className="px-4 py-4">
                          <div className="font-medium text-gray-900">{partner.name}</div>
                          <div className="text-xs text-gray-500">{partner.contactPerson?.name || 'N/A'}</div>
                        </td>
                        <td className="px-4 py-4 text-sm text-gray-700">
                          {partner.documents?.businessLicense || partner.adminNotes?.match(/Registration:\s*([^,]+)/)?.[1] || 'N/A'}
                        </td>
                        <td className="px-4 py-4">
                          <div className="text-sm text-gray-700">
                            {partner.description?.match(/Specialization:\s*([^.]+)/)?.[1]?.trim() || partner.contactPerson?.designation || 'General Physician'}
                          </div>
                          {partner.description?.match(/Qualification:\s*([^.]+)/)?.[1] && (
                            <div className="text-xs text-gray-500 line-clamp-1">
                              {partner.description.match(/Qualification:\s*([^.]+)/)?.[1].trim()}
                            </div>
                          )}
                        </td>
                        <td className="px-4 py-4 text-sm">
                          <div className="text-gray-700">{partner.email || partner.contactPerson?.email || 'N/A'}</div>
                          <div className="text-gray-600">{partner.phone || partner.contactPerson?.phone || 'N/A'}</div>
                        </td>
                        <td className="px-4 py-4 text-sm text-gray-700 max-w-xs">
                          <div className="line-clamp-2">
                            {partner.address?.city || 'N/A'}, {partner.address?.state || 'N/A'}
                          </div>
                        </td>
                        <td className="px-4 py-4">
                          {partner.adminNotes?.match(/NGO Offer:\s*([^,]+)/)?.[1] ? (
                            <span className="px-2 py-1 bg-green-100 text-green-700 rounded-full text-xs font-medium">
                              {partner.adminNotes.match(/NGO Offer:\s*([^,]+)/)?.[1].trim()}
                            </span>
                          ) : 'N/A'}
                        </td>
                        <td className="px-4 py-4">
                          <div className="flex gap-2">
                            <button className="px-3 py-1.5 bg-blue-600 text-white text-xs rounded-lg hover:bg-blue-700 transition-colors flex items-center gap-1">
                              <BiEdit size={14} />
                              Edit
                            </button>
                            <button 
                              onClick={() => handleDelete(partner._id)}
                              className="px-3 py-1.5 bg-red-600 text-white text-xs rounded-lg hover:bg-red-700 transition-colors flex items-center gap-1"
                            >
                              <BiTrash size={14} />
                              Delete
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan="7" className="px-4 py-12 text-center text-gray-500">
                        <div className="text-4xl mb-2">üè•</div>
                        No healthcare partners found
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>

          {/* Food Partners */}
          <div className="bg-white rounded-xl shadow-sm border p-6">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-xl font-semibold text-orange-900 flex items-center gap-2">
                üçΩÔ∏è Food Partners ({foodPartners.length})
              </h2>
            </div>
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="bg-gradient-to-r from-orange-50 to-red-50 border-b-2 border-orange-200">
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Business Name</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">FSSAI No.</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Cuisine/Services</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Contact</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Location</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Discount</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {foodPartners.length > 0 ? (
                    foodPartners.map((partner, idx) => (
                      <tr key={partner._id} className={`border-b hover:bg-gray-50 transition-colors ${idx % 2 === 0 ? 'bg-white' : 'bg-gray-50/30'}`}>
                        <td className="px-4 py-4">
                          <div className="font-medium text-gray-900">{partner.name}</div>
                          <div className="text-xs text-gray-500">{partner.contactPerson?.name || 'N/A'}</div>
                        </td>
                        <td className="px-4 py-4 text-sm text-gray-700">
                          {partner.adminNotes?.match(/FSAI:\s*([^,]+)/)?.[1]?.trim() || partner.documents?.businessLicense || 'N/A'}
                        </td>
                        <td className="px-4 py-4">
                          <div className="text-sm text-gray-700">{partner.description || 'Multi-cuisine'}</div>
                          {partner.description && (
                            <div className="text-xs text-gray-500 line-clamp-1">{partner.description.substring(0, 50)}...</div>
                          )}
                        </td>
                        <td className="px-4 py-4 text-sm">
                          <div className="text-gray-700">{partner.email || partner.contactPerson?.email || 'N/A'}</div>
                          <div className="text-gray-600">{partner.phone || partner.contactPerson?.phone || 'N/A'}</div>
                        </td>
                        <td className="px-4 py-4 text-sm text-gray-700 max-w-xs">
                          <div className="line-clamp-2">
                            {partner.address?.city || 'N/A'}, {partner.address?.state || 'N/A'}
                          </div>
                        </td>
                        <td className="px-4 py-4">
                          {partner.adminNotes?.match(/Special Offer:\s*([^,]+)/)?.[1] ? (
                            <span className="px-2 py-1 bg-orange-100 text-orange-700 rounded-full text-xs font-medium">
                              {partner.adminNotes.match(/Special Offer:\s*([^,]+)/)?.[1].trim()}
                            </span>
                          ) : 'N/A'}
                        </td>
                        <td className="px-4 py-4">
                          <div className="flex gap-2">
                            <button className="px-3 py-1.5 bg-blue-600 text-white text-xs rounded-lg hover:bg-blue-700 transition-colors flex items-center gap-1">
                              <BiEdit size={14} />
                              Edit
                            </button>
                            <button 
                              onClick={() => handleDelete(partner._id)}
                              className="px-3 py-1.5 bg-red-600 text-white text-xs rounded-lg hover:bg-red-700 transition-colors flex items-center gap-1"
                            >
                              <BiTrash size={14} />
                              Delete
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan="7" className="px-4 py-12 text-center text-gray-500">
                        <div className="text-4xl mb-2">üçΩÔ∏è</div>
                        No food partners found
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      )}
    </>
  );
}



